export default {
	data() {
		return {
			contentShow1: true,
			contentShow2: true,
			contentShow3: true
		};
	},
	methods: {
		control1() {
			this.contentShow1 = !this.contentShow1;
		},
		control2() {
			this.contentShow2 = !this.contentShow2;
		},
		control3() {
			this.contentShow3 = !this.contentShow3;
		}
	}
};
